/* global styles */
/**
* z-index settings:
*
*
*
*
*/
html {
  box-sizing: border-box; }

body {
  margin: 0;
  color: #444444;
  font: 400 15px/30px "BloggerSans", "Tahoma", sans-serif; }

img {
  max-width: 100%;
  height: auto; }

a {
  color: inherit;
  text-decoration: none; }

*,
*::before,
*::after {
  box-sizing: inherit; }

/**
*  Service Classes
*/
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }
